___CSS_LOADER_EXPORT___.push([module.id, `/* General layout and styling */
.quiz-container {
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    font-family: 'Arial', sans-serif;
  }
  
  h1 {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 30px;
  }
  
  /* Category dropdown and date picker */
  .category-selector, .date-picker {
    margin-bottom: 20px;
  }
  
  select, .react-datepicker__input-container input {
    padding: 10px;
    font-size: 1rem;
    width: 250px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  /* Questions */
  .questions-container {
    margin-top: 20px;
  }
  
  .question-box {
    background-color: #f9f9f9;
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .question-text {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
  }
  
  /* Option styles */
  .option {
    margin: 10px 0;
    font-size: 1.2rem;
    color: #555;
  }
  
  input[type="radio"] {
    margin-right: 10px;
  }
  
  label {
    cursor: pointer;
  }
  
  .submit-button {
    padding: 12px 25px;
    font-size: 1.2rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
  }
  
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  /* Feedback styles */
  .feedback {
    margin-top: 15px;
    font-weight: bold;
  }
  
  .feedback.correct {
    color: green;
  }
  
  .feedback.incorrect {
    color: red;
  }
  
  .correct-answer {
    margin-top: 15px;
    font-size: 1.2rem;
    font-weight: bold;
    color: #007bff;
  }
